.row-wrapper {
  width: 100%;
  padding-right: $gutter-vertical;
  padding-left: $gutter-vertical;
  margin-right: auto;
  margin-left: auto;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: - $gutter-vertical;
  margin-left: - $gutter-vertical;
  &:not(:last-child) {
    margin-bottom: $gutter-vertical;
  }
}

.no-gutters {
  margin-right: 0;
  margin-left: 0;
}

.no-gutters > .col,
.no-gutters > [class*="col-"] {
  padding-right: 0;
  padding-left: 0;
}

